/*动画 fade,bounce*/
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.fade-enter-active,
.fade-leave-active {
  opacity: 0;
}
.bounce-enter-active {
  -webkit-animation: bounce-in 0.5s;
          animation: bounce-in 0.5s;
}
.bounce-leave-active {
  -webkit-animation: bounce-out 0.5s;
          animation: bounce-out 0.5s;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
@keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
.moving {
  margin-bottom: 20px;
  margin: 0 auto;
  width: 960px;
}
.moving p {
  color: red;
}
.moving .tit {
  width: 560px;
  margin: 0 auto;
  margin-top: 20px;
}
.moving .tit h1 {
  display: inline-block;
  width: 126px;
  font-size: 20px;
  color: #000;
}
.moving .tit .locat {
  position: relative;
  display: inline-block;
}
.moving .moviesList {
  width: 950px;
  margin: 10px auto 0;
  min-height: 500px;
}
.moving .moviesList .movie-container {
  margin: 0 auto;
  width: 560px;
  border-top: 1px dashed #ccc;
  margin-top: -10px;
}
.moving .moviesList .movie-container .movieTag {
  padding-top: 20px;
  width: 118px;
  height: 270px;
  overflow: hidden;
  font-size: 12px;
  text-align: center;
  display: inline-block;
  margin: 0 20px 20px 0;
}
.moving .moviesList .movie-container .movieTag ul {
  margin: 0 auto;
  padding: 0;
}
.moving .moviesList .movie-container .movieTag ul li {
  list-style: none;
}
.moving .moviesList .movie-container .movieTag a {
  cursor: pointer;
  text-decoration: none;
}
.moving .moviesList .movie-container .movieTag .film-pic {
  height: 180px;
  margin-bottom: 12px;
  overflow: hidden;
}
.moving .moviesList .movie-container .movieTag .film-pic a img {
  width: 128px;
  vertical-align: middle;
}
.moving .moviesList .movie-container .movieTag .film-name {
  font-size: 14px;
  white-space: nowrap;
  height: 22px;
  overflow: hidden;
}
.moving .moviesList .movie-container .movieTag .film-name a {
  display: inline-block;
  word-spacing: normal;
  height: 24px;
  line-height: 24px;
  text-decoration: none;
  color: black;
}
.moving .moviesList .movie-container .movieTag .film-rate {
  display: inline-block;
  margin: 4px auto 2px;
  height: 19px;
}
.moving .moviesList .movie-container .movieTag .film-rate .el-rate {
  display: inline-block;
}
.moving .moviesList .movie-container .movieTag .film-rate .el-rate .el-rate__icon {
  font-size: 12px;
}
.moving .moviesList .movie-container .movieTag .film-rate .rateNum {
  display: inline-block;
  font-size: 12px;
}
.moving .moviesList .movie-container .movieTag .film-button span {
  display: block;
  margin: 0 auto;
  text-align: center;
  width: 90px;
  height: 24px;
  line-height: 24px;
  background-color: #268dcd;
  border-radius: 2px;
}
.moving .moviesList .movie-container .movieTag .film-button span a {
  color: #fff;
}
.moving .moviesList .movie-container .load-more {
  cursor: pointer;
