  text-decoration: underline;
}
/* 默认不显示下划线，保持页面简洁 */
ins,
a {
  text-decoration: none;
}
/* 代码字体 */
code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em;
}
/* 清理浮动 */
.clearfix:after {
  display: block;
  visibility: hidden;
  clear: both;
  height: 0;
  content: " ";
  font-size: 0;
}
.clearfix {
  zoom: 1;
  /* for IE6 IE7 */
}
/* 隐藏, 通常用来与 JS 配合 */
.fn-hide {
  display: none;
}
/* 设置内联, 减少浮动带来的bug */
.fn-left,
.fn-right {
  display: inline;
}
.fn-left {
  float: left;
}
.fn-right {
  float: right;
}
/* 单行文字溢出时出现省略号，需设定宽度 */
.fn-text-overflow {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
/* 简单的渐变 */
.fn-linear {
  background: -webkit-gradient(linear, left top, left bottom, from(#fbfbfb), to(#f2f2f2));
  background: linear-gradient(top, #fbfbfb, #f2f2f2);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fbfbfb', endColorstr='#f2f2f2');
}
/* 浅色的渐变 */
.fn-linear-light {
  background: -webkit-gradient(linear, left top, left bottom, from(#fdfdfd), to(#eee));
  background: linear-gradient(top, #fdfdfd, #eee);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fdfdfd', endColorstr='#eee');
}
/* chrome 下字体过小的问题 */
.fn-webkit-adjust {
  -webkit-text-size-adjust: none;
}
/* placeholder颜色*/
::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #999;
}
:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #999;
}
::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #999;
}
:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #999;
}
/*一些页面宽度*/
.grid-950 {
  width: 950px;
  margin: 0 auto;
}
/*.container{
    padding-top: 10px;
    background: #f3f3f3;
    min-width: 950px;
}*/
/*动画 fade,bounce*/
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.fade-enter-active,
.fade-leave-active {
  opacity: 0;
}
.bounce-enter-active {
  -webkit-animation: bounce-in 0.5s;
          animation: bounce-in 0.5s;
}
.bounce-leave-active {
  -webkit-animation: bounce-out 0.5s;
          animation: bounce-out 0.5s;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
@keyframes bounce-out {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
50% {
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
.container-moving {
  width: 950px;
  margin: 30px auto;
}
.container-moving .content {
  min-height: 420px;
}
.container-moving .content h1 {
  word-break: break-all;
  display: block;
  font-size: 25px;
  font-weight: bold;
  color: #494949;
  padding: 0 0 15px 0;
}
.container-moving .content h1 .year {
  color: #888;
}
.container-moving .content .detail .left-side {
  float: left;
  width: 590px;
  padding-right: 40px;
}
.container-moving .content .detail .left-side .actor-list {
  margin-bottom: 30px;
}
.container-moving .content .detail .left-side .actor-list .subject {
  width: 415px;
  float: left;
}
.container-moving .content .detail .left-side .actor-list .subject .mainpic {
  margin: 3px 12px 0 0;
  max-width: 155px;
  overflow: hidden;
  text-align: center;
  float: left;
  height: 140px;
}
.container-moving .content .detail .left-side .actor-list .subject .mainpic a img {
  margin-bottom: 10px;
  max-width: 100px;
}
.container-moving .content .detail .left-side .actor-list .subject .mainpic .more-pic {
  text-decoration: none;
  color: #BBBBBB;
  font-size: 12px;
  text-align: center;
}
.container-moving .content .detail .left-side .actor-list .subject .info {
  float: left;
  max-width: 248px;
  word-wrap: break-word;
}
.container-moving .content .detail .left-side .actor-list .subject .info .p1 {
  line-height: 150%;
  color: #666666;
}
.container-moving .content .detail .left-side .actor-list .subject .info .attrs {
  color: #37a;
}
.container-moving .content .detail .left-side .actor-list .people-sroce {
  float: left;
  width: 155px;
  margin: 2px 0 0 0;
  padding: 0 0 0 15px;
  border-left: 1px solid #eaeaea;
  color: #aaa;
}
.container-moving .content .detail .left-side .actor-list .people-sroce .score {
  font-size: 25px;
  margin-right: 10px;
}
.container-moving .content .detail .left-side .actor-list .people-sroce .comment-num {
  margin-top: -20px;
  margin-left: 45px;
  color: #666699;
}
.container-moving .content .detail .left-side .insterest-people {
  clear: both;
  padding: 20px 0 3px;
}
.container-moving .content .detail .left-side .insterest-people .top button {
  display: inline-block;
  width: 50px;
  height: 25px;
  background: #fae9da;
  border: 1px solid #ca6445;
  color: #333;
  margin-right: 10px;
  border-radius: 4px;
}
.container-moving .content .detail .left-side .insterest-people .score-to .el-rate {
  margin-left: 5px;
  display: inline-block;
}
.container-moving .content .detail .left-side .insterest-people .score-to .el-rate .el-rate__icon {
  font-size: 12px;
}
.container-moving .content .detail .left-side .gtleft {
  padding: 4px 4px 0 0;
  float: left;
}
.container-moving .content .detail .left-side .gtleft .ul {
  padding-top: 6px;
}
.container-moving .content .detail .left-side .gtleft .ul li {
  float: left;
  display: inline;
  line-height: 14px;
  margin-right: 15px;
}
.container-moving .content .detail .left-side .gtleft .ul li img {
  max-width: 100%;
  margin-right: 5px;
}
.container-moving .content .detail .left-side .gtleft .ul li a {
  text-decoration: none;
  color: #37a;
}
.container-moving .content .detail .left-side .summary {
  float: left;
  clear: both;
  margin-top: 20px;
}
.container-moving .content .detail .left-side .summary .summary-title {
  color: #007722;
  font-size: 16px;
}
.container-moving .content .detail .right-side {
  float: right;
  width: 310px;
}
.container-moving .content .detail .right-side .ticket {
  padding: 20px 0;
  margin-bottom: 40px;
  background-color: #F0F3F5;
  color: #999;
  font-size: 13px;
  text-align: center;
}
.container-moving .content .detail .right-side .ticket a {
  display: inline-block;
  width: 160px;
  line-height: 30px;
  background: #268dcd;
  border-radius: 2px;
  color: #fff;
}
.container-moving .content .detail .right-side .game-pic img {
  display: inherit;
  margin: 0 auto;
}
.container-moving .content .detail .right-side .qrcode-app {
  margin: 20px 0;
  border: none;
}
